@import "../../../assets/mixins/mixins.scss";
$contact-color: #8a8687;
@mixin input-border() {
  padding: 10px;
  box-shadow: inset 0 -1px 0 0 $contact-color;
  &:hover {
    box-shadow: inset 0 -2px 0 0 $contact-color;
  }
  &:focus {
    box-shadow: inset 0 -2px 0 0 red;
  }
}
.contact {
  width: 92%;
  margin: 90px auto 0 auto;
  &__title {
    @include spa-font();
    font-size: 2.3em;
    background-color: var(--container-color);
    padding: 80px;
    text-align: center;
  }
  &__wrapper {
    width: 100%;
    padding: 55px 500px;
  }
  &__divider {
    width: 50px;
  }
  &__title-2 {
    margin-top: 30px;
    text-align: center;
    @include spa-font();
    font-size: 1.15em;
  }
  &__text {
    margin: 55px 0;
    text-align: center;
    @include spa-font();
    line-height: 1.8em;
  }
  &__form {
    width: 100%;
    @include flex-params();
    flex-direction: column;
  }
  &__input {
    width: 100%;
    display: flex;
    justify-content: space-between;
    &-name {
      width: 50%;
      margin-right: 10px;
      @include spa-font();
      @include input-border();
    }
    &-email {
      width: 50%;
      margin-left: 10px;
      @include spa-font();
      @include input-border();
    }
  }
  &__textarea {
    width: 100%;
    resize: none;
    margin-top: 10px;
    @include spa-font();
    @include input-border();
    &::placeholder {
      @include spa-font();
    }
  }
  &__btn {
    width: 100%;
    background-color: #000;
    color: #fff;
    margin-top: 15px;
    padding: 10px;
    @include spa-font();
    font-size: 1.15em;
    font-weight: 700;
    transition: all 0.3s;
    &:hover {
      background-color: $contact-color;
    }
  }
}

@media screen and(max-width: 574px) {
  .contact {
    width: 100%;
    &__wrapper {
      padding: 0;
    }
  }
}
