@import "../../../assets/mixins/mixins.scss";
$orderColor: lightgrey;
$borderColor: 1px solid $orderColor;
.order {
  width: 92%;
  margin: 90px auto 0 auto;
  &__title {
    @include spa-font();
    font-size: 2.3em;
    background-color: var(--container-color);
    padding: 80px;
    text-align: center;
  }
  &__container {
    display: flex;
  }
  &__aside-left {
    width: 15%;
    display: flex;
    flex-direction: column;
    border-right: $borderColor;
  }
  &__menu {
    width: 70%;
    &-header {
      width: 30%;
      margin: 0 auto;
      border-bottom: $borderColor;
      text-align: center;
      padding: 30px 0 20px 0;
    }
    &-timing {
      margin-top: 10px;
    }
  }
  &__aside-right {
    width: 15%;
    border-left: $borderColor;
    display: flex;
    flex-direction: column;
    &-header {
      display: flex;
      justify-content: space-between;
      padding: 20px;
      border-bottom: $borderColor;
      &-text {
        color: rgba(41, 35, 37, 0.8);
      }
    }
    &-cart {
      display: flex;
      flex-direction: column;
      min-height: 200px;
      max-height: 300px;
      overflow-y: auto;
      overflow-x: hidden;
      &-svg {
        fill: $orderColor;
        margin: 30px 0 0 100px;
      }
      &-caption {
        width: 80%;
        margin: 20px auto;
        color: $orderColor;
      }
      &-item {
        width: 100%;
        height: 30%;
        display: flex;
        justify-content: space-between;
        padding: 20px 25px;
        position: relative;
        transition: background-color 0.2s;
        &-price {
          z-index: 2;
          overflow: hidden;
          position: absolute;
          top: 20px;
          right: 10px;
          padding-left: 15px;
          transition: all 0.2s;
          background-color: #fff;
        }
        &:hover {
          background-color: $orderColor;
        }

        &:hover .order__aside-right-cart-btns {
          transform: translateX(-40px);
          opacity: 1;
          visibility: visible;
        }
        &:hover .order__aside-right-cart-item-price {
          transform: translateX(-40px);
          background-color: $orderColor;
        }
      }
      &-btns {
        position: absolute;
        display: flex;
        flex-direction: column;
        top: 5px;
        right: -20px;
        opacity: 0;
        visibility: hidden;
        transition: all 0.2s;
        padding-left: 20px;
        background-color: $orderColor;
      }
      &-btn {
        background-color: transparent;
        color: #000;

        border-radius: 50%;
        font-size: 20px;
        padding: 0 5px;
        &:hover {
          background-color: grey;
        }
      }
    }
    &-divider {
      width: 90%;
      margin: 10px auto 0 auto;
    }
    &-form {
      padding: 20px;
      &-input {
        resize: none;
        border: $borderColor;
        @include spa-font();
        box-shadow: 0 0 2px 0 black;
      }
      &-btn-request {
        font-size: 18px;
        cursor: pointer;
      }
      &-caption {
        margin: 10px 0;
      }
      &-btn-submit {
        @include btn-submit();
        height: 50px;
      }
    }
  }
  &__btn-option {
    padding: 13px 36px;
    @include spa-font();
    font-size: 14px;
    color: rgb(41, 35, 37);
    border-left: 3px solid transparent;
    &:hover {
      background-color: rgb(234, 233, 234);
    }
    &_selected {
      background-color: rgb(234, 233, 234);
      border-left: 3px solid #635e60;
    }
  }
  &__accordion-content {
    display: flex;
    flex-direction: column;
  }
}

@media screen and(max-width: 574px) {
  .order {
    width: 100%;
    &__container {
      flex-direction: column;
    }
    &__aside-left {
      width: 100%;
    }
    &__menu {
      width: 100%;
      &-header {
        width: 100%;
      }
    }
    &__aside-right {
      width: 100%;
      &-cart-svg {
        margin: 30px 0 0 220px;
      }
    }
  }
}
