@import "../../../assets/mixins/mixins.scss";
.home__grid {
  width: 100%;
  display: grid;
  grid-template:
    "cell-1 cell-1 cell-2 cell-2 cell-3 cell-3" 700px
    "cell-4 cell-4 cell-4 cell-4 cell-4 cell-4 " 120px
    "cell-5 cell-5 cell-5 cell-6 cell-6 cell-6 " 440px;
  grid-template-columns: repeat(6, 1fr);
  grid-gap: 20px;
}
.cell-1 {
  grid-area: cell-1;
}
.cell-2 {
  grid-area: cell-2;
  background-color: var(--container-color);
  @include flex-params();
  flex-direction: column;
  padding: 0 160px;
}
.cell-3 {
  grid-area: cell-3;
}
.cell-4 {
  grid-area: cell-4;
  background-image: url("../../../assets/img/bg1.jpg");
  @include flex-params();
}
.cell-5 {
  grid-area: cell-5;
  position: relative;
}
.cell-6 {
  grid-area: cell-6;
  position: relative;
}
@media screen and (max-width: 574px) {
  .home__grid {
    grid-template:
      "cell-1" auto
      "cell-2" 300px
      "cell-3" auto
      "cell-4" 300px
      "cell-5" 300px
      "cell-6" 300px;
    grid-template-columns: 1fr;
  }
  .cell-2 {
    padding: 0;
  }
}
