.order-celebration {
  width: 100%;
  padding: 15px 110px;
  text-align: center;
  &__text {
    padding: 20px;
  }
  &__grid {
    width: 100%;
    display: grid;
    grid-template:
      "cell1 cell2 cell3" 280px
      "cell4 cell5 cell6" 280px;
    grid-template-columns: 1fr 1fr 1fr;
    grid-gap: 40px;
    &-cell {
      position: relative;
      overflow: hidden;
      cursor: pointer;
      &-icon {
        position: absolute;
        bottom: 15px;
        right: 20px;
        font-size: 24px;
        color: rgb(24, 24, 24);
        user-select: none;
      }
      &_1 {
        grid-area: cell1;
      }
      &_2 {
        grid-area: cell2;
      }
      &_3 {
        grid-area: cell3;
      }
      &_4 {
        grid-area: cell4;
      }
      &_5 {
        grid-area: cell5;
      }
      &_6 {
        grid-area: cell6;
      }
      &:hover .order-celebration__img,
      &:hover .order-celebration__img-caption {
        transform: translateY(-80px);
      }
    }
  }
  &__img {
    width: 100%;
    height: 210px;
    object-fit: cover;
    transition: all 0.2s ease-in;
    &-caption {
      width: 100%;
      height: 70px;
      background-color: #fff;
      text-align: initial;
      padding: 10px;
      transition: all 0.2s ease-in;
    }
  }
}
@media screen and(max-width:574px) {
  .order-celebration {
    padding: 15px 0;
    &__grid {
      grid-template:
        "cell1 cell2 cell3" 320px
        "cell4 cell5 cell6" 320px;
    }
    &__grid-cell-icon {
      display: none;
    }
    &__grid-cell:hover &__grid-cell-icon {
      display: initial;
    }
  }
}
