@import "../../assets/mixins/mixins.scss";
.carousel__modal {
  width: calc(100vw - 10px);
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  background-color: #f7e9e4b4;
  z-index: 20;
  &-container {
    width: calc(100vw - 100px);
    height: calc(100vh - 100px);
    margin: 50px auto;
    background-color: white;
    border-radius: 10px;
    padding: 30px 0px;
    position: relative;
    display: flex;
  }
  &-img {
    width: 50%;
    height: 100%;
    object-fit: contain;
  }
  &-text {
    display: flex;
    flex-direction: column;
  }
  &-description {
    font-style: italic;
    margin-top: 20px;
  }
  &-btn {
    position: absolute;
    top: 10px;
    right: 10px;
    background: transparent;
    border-radius: 5px;
    padding: 2px 3px;
    &:hover {
      background-color: rgb(175, 161, 161);
      color: #fff;
    }
  }
}
@media screen and(max-width:574px) {
  .carousel__modal-container {
    flex-direction: column;
    text-align: center;
  }
  .carousel__modal-img {
    width: 100%;
    max-height: 60%;
  }
}
