@import "../../../assets/mixins/mixins.scss";
@font-face {
  font-family: "Palatino-Linotype";
  src: local("Palatino-Linotype"),
    url(./palatino-linotype.ttf) format("truetype");
}
.home {
  width: 92%;
  margin: 90px auto 0 auto;
  min-height: calc(100vh - 390px);
  display: flex;
  flex-direction: column;
  &__title {
    font-family: "Palatino-Linotype", serif;
    font-size: 17px;
    letter-spacing: 0.25em;
    margin: 40px auto;
  }
  &__img {
    object-fit: cover;
    width: 100%;
    max-height: 100%;
  }
  &__title-seo {
    text-align: center;
  }
  &__title-pitch {
    @include spa-font();
    font-size: 1.6em;
    letter-spacing: 0.1em;
  }
  &__btn {
    background-color: #fff;
    margin-top: 50px;
    padding: 15px 40px;
    @include spa-font();
    font-size: 1.145em;
    font-weight: 700;
    transition: all 0.1s ease-in;
    cursor: pointer;
    &:hover {
      box-shadow: inset 0px 0px 0px 2px black;
    }
    &_corner {
      position: absolute;
      bottom: 50px;
      right: 60px;
    }
  }
  &__title-follow-me {
    text-align: center;
    padding: 40px;
    @include spa-font();
    font-size: 2.3em;
  }
}

@media screen and (max-width: 574px) {
  .home {
    width: 100%;
    &__btn {
      padding: 5px 15px;
    }
    &__btn_corner {
      top: 50px;
      right: 10px;
      bottom: auto;
    }
  }
}
