@import "../../assets/mixins/mixins.scss";
.footer__modal {
  width: calc(100vw - 10px);
  height: calc(100vh + 300px);
  position: fixed;
  top: 0;
  left: 0;
  background-color: #f7e9e4b4;
  z-index: 20;
}
.modal {
  width: 20%;
  margin: 300px auto;
  background-color: white;
  border-radius: 10px;
  padding: 10px;
  @include flex-params();
  justify-content: space-between;
  position: relative;
  &__btn {
    position: absolute;
    top: 10px;
    right: 10px;
    background: transparent;
    border-radius: 5px;
    padding: 2px 3px;
    &:hover {
      background-color: red;
      color: #fff;
    }
  }
}
@media screen and(max-width:547px) {
  .modal {
    width: 90%;
  }
}
