@import "../../assets/mixins/mixins.scss";
.order__modal {
  width: 100%;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  background-color: #7e7673b4;
  z-index: 20;
  &-container {
    width: 500px;
    height: 700px;
    margin: 50px auto;
    background-color: white;
    position: relative;
    display: flex;
    flex-direction: column;
    box-shadow: 0px 0px 6px 0px rgba(0, 0, 0, 0.644);
  }
  &-img {
    width: 100%;
    height: 50%;
    object-fit: cover;
  }
  &-wrapper {
    display: flex;
    flex-direction: column;
    padding: 25px;
    text-align: initial;
  }
  &-price {
    font-style: italic;
    margin-top: 20px;
  }
  &-btn {
    position: absolute;
    top: -10px;
    right: -10px;
    width: 25px;
    height: 25px;
    border-radius: 50%;
    background-color: #000;
    color: #fff;
    &:hover {
      background-color: rgb(175, 161, 161);
      color: #fff;
    }
  }
  &-text {
    margin-top: 20px;
    font-size: 16px;
  }
  &-divider {
    width: 100%;
    margin: 15px 0;
  }
  &-form {
    width: 100%;
    &-text {
      width: 100%;
      padding: 10px;
      resize: none;
      @include spa-font();
      border: 1px solid lightgray;
    }
    &-warning {
      font-size: 12px;
    }
    &-quantity {
      display: flex;
      justify-content: space-between;
      padding: 10px 0;
      &-title {
        font-size: 18px;
      }
      &-btns {
        display: flex;
        align-items: center;
      }
      &-reflect {
        padding: 0 10px;
      }
      &-btn {
        border: 2px solid black;
        border-radius: 50%;
        font-size: 20px;
        font-weight: 700;
        padding: 0 5px;
        &:first-of-type {
          padding: 0 7px;
        }
      }
    }
    &-btn {
      @include btn-submit();
    }
  }
}

@media screen and(max-width:574px) {
  .order__modal {
    width: 100%;
    height: 100%;
    &-container {
      width: 90%;
      height: 90%;
    }
  }
}
