@import "../../../assets/mixins/mixins.scss";
.about {
  width: 92%;
  margin: 90px auto 0 auto;
  background-color: var(--container-color);
  @include flex-params();
  flex-direction: column;
  padding: 65px 550px;
  &__title {
    @include spa-font();
    font-size: 2.3em;
  }
  &__divider {
    width: 50px;
    margin-top: 40px;
  }
  &__title-2 {
    margin-top: 35px;
    @include spa-font();
    letter-spacing: 0.1em;
  }
  &__img {
    margin: 85px 0 30px 0;
    width: 300px;
    height: 300px;
    border-radius: 50%;
    background-image: url("../../../assets/img/hanna.jpg");
    background-size: 190%;
    background-repeat: no-repeat;
    background-position: top 0px left -130px;
  }
  &__text {
    margin-top: 35px;
    @include spa-font();
    text-align: center;
    line-height: 1.5em;
  }
}

@media screen and(max-width:1199px) {
  .about {
    width: 100%;
    padding: 50px 200px;
  }
}
@media screen and(max-width:574px) {
  .about {
    width: 100%;
    padding: 0;
  }
}
