@import "../../assets/mixins/mixins.scss";
.footer {
  width: 100%;
  height: 300px;
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
  &__title {
    @include spa-font();
    font-size: 1.2em;
    margin: 50px 0;
  }
  &__subscribe {
    display: flex;
    flex-direction: column;
    width: 340px;
    &-input {
      width: 100%;
      height: 33px;
      padding-left: 20px;
      border: 1px solid #000;
      &:hover {
        border: 2px solid #000;
      }
      &:focus {
        border: 2px solid red;
      }
      &-wrong {
        background-color: red;
      }
    }
    &-btn {
      width: 100%;
      height: 33px;
      background-color: #000;
      color: #fff;
      @include spa-font();
      font-size: 1.2em;
      font-weight: 700;
      margin-top: 5px;
      cursor: pointer;
      &:hover {
        background: grey;
      }
    }
  }
  &__signature {
    width: 100%;
    text-align: center;
    padding: 20px;
    background: var(--container-color);
    position: absolute;
    bottom: 0;
  }
}
