@import url("https://fonts.googleapis.com/css2?family=Raleway:wght@100;200;300;400;500;600;700;800;900&display=swap");
@mixin spa-font() {
  font-family: "Raleway", sans-serif;
  font-size: 15px;
}
@mixin flex-params() {
  display: flex;
  justify-content: center;
  align-items: center;
}
:root {
  --container-color: #f7e9e4;
}
@mixin btn-submit() {
  width: 100%;
  height: 33px;
  background-color: #000;
  color: #fff;
  @include spa-font();
  font-size: 1.2em;
  font-weight: 700;
  margin-top: 5px;
  cursor: pointer;
  &:hover {
    background: grey;
  }
}
