@import "../../assets/mixins/mixins.scss";
$font-color: #292325;
$link-hover-color: #998d8f;
.header {
  width: 100%;
  position: fixed;
  top: 0;
  z-index: 10;
  &__nav {
    width: 92%;
    margin: 0 auto;
    display: flex;
    height: 90px;
    background-color: #fff;
    border-bottom: 3px solid #000;
    position: relative;
  }
  &__router {
    flex: 1 0 auto;
    margin-left: 125px;
    @include flex-params();
    &-link {
      padding: 50px;
      @include spa-font();
      color: $font-color;
      &_selected {
        color: $link-hover-color;
      }
    }
  }
  &__social {
    flex: 0 0 125px;
    @include flex-params();
    &-link {
      margin: 12px;
    }
  }
  &__nav-toggle {
    display: none;
    padding: 0;
    position: absolute;
    top: 30px;
    right: 0px;
    cursor: pointer;
    background: transparent;
    width: 25px;
    height: 25px;
    transition: all 0.1s;
    &::after {
      content: "";
      width: 100%;
      height: 2px;
      background: #000;
      position: absolute;
      bottom: 33.333%;
      left: 0;
    }
    &::before {
      content: "";
      width: 100%;
      height: 2px;
      background: #000;
      position: absolute;
      top: 33.333%;
      left: 0;
    }
    &_active {
      &::after {
        top: #{calc(50% - 4px)};
        transform: rotate(-45deg);
        transition: all 0.1s;
      }
      &::before {
        bottom: #{calc(50%)};
        transform: rotate(45deg);
        transition: all 0.1s;
      }
    }
  }
}
.switch-wrapper {
  position: relative;
}
.switch-wrapper > div {
  width: 100%;
  position: absolute;
}

@media screen and (max-width: 574px) {
  .header {
    &__nav {
      width: 100%;
      flex-direction: column;
      z-index: 10;
    }
    &__router {
      flex-direction: column;
      width: 100vw;
      height: calc(90vh - 90px);
      background-color: #fff;
      margin: 90px 0 0 0;
      transform: translateY(-120%);
      transition: all 0.3s ease-in;
      &_active {
        transform: translateY(0);
      }
      z-index: 5;
    }
    &__social {
      width: 100vw;
      background: #fff;
      transform: translateY(-1000%);
      transition: all 0.3s ease-in;
      &_active {
        transform: translateY(0);
      }
      z-index: 5;
    }
    &__nav-toggle {
      display: block;
    }
  }
}
