.carousel {
  width: 100%;
  height: 210px;
  position: relative;
  &__slider {
    height: 100%;
  }
  &__slide {
    height: 100%;
    padding: 0;
    &-wrapper {
      height: 210px;
      margin: 0 10px;
      position: relative;
      &:hover .carousel__img-hover {
        opacity: 1;
        visibility: visible;
      }
    }
  }
  &__img {
    object-fit: cover;
    width: 100%;
    height: 100%;
    &-hover {
      position: absolute;
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
      background-color: rgba(0, 0, 0, 0.308);
      opacity: 0;
      visibility: hidden;
      cursor: pointer;
    }
  }
  &__btn {
    position: absolute;
    top: 75px;
    height: 50px;
    width: 50px;
    background: transparent;
    z-index: 1;
    font-size: 34px;
    color: #999;
    &:hover {
      color: #666;
    }
  }
  &__back-button {
    left: 0;
  }
  &__next-button {
    right: 0;
  }
}
@media screen and(max-width:547px) {
  .carousel {
    height: 400px;
    &__slide-wrapper {
      height: 400px;
    }
    &__btn {
      top: 180px;
    }
  }
}
